import { useContext } from "react";
import { MainContext } from "../../contexts/contexts";

const Header = ({ type }) => {
  const data = useContext(MainContext);

  const text = () => {
    const remainder = data.length % 10;
    const isExclusion = data.length % 100 === 11;
    return remainder === 1 && !isExclusion ? '' :
           remainder >= 2 && remainder <= 4 && !isExclusion ? '' :
           '';
  };

  const subtext = () => {
    const types = {
      'new': '  ',
      'cash': '  ',
      'fr': '  '
    };
    return types[type] || ' ';
  }
    
  return (
    <header>
      <div>
        <h1 className="title">     Word&nbsp;&nbsp;<span style={{fontSize: '14px', verticalAlign: 'top', color: 'var(--color-error)'}}>&beta;-</span></h1>
        <div>
          <p className="subtitle">
            &nbsp;<span>{data.length}</span>&nbsp;{text()}&nbsp;
            {data.length !== 0 && <span>{subtext()}</span>}
          </p>
        </div>
      </div>
    </header>
  )
};

export default Header;